@import url(https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&display=swap);

footer {
    width: 100%;
    background: var(--thm-black);
    padding-top: 3.7rem;
    margin-top: 6rem;
    display: flex;
    justify-content: center;
    color: #9796a2;
}


.copy-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border-top: 1px solid rgba(160, 158, 158, 0.5);
    padding: 0.9rem 0;
}

.copy-container span {
    font-size: 1rem;
}

.social-icons .icon {
    margin-left: 10px;
    cursor: pointer;
    color: var(--thm-primary);
}

.social-icons .icon:hover {
    color: #fff;
}

.sections-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
    grid-gap: 1rem;
    gap: 1rem;
    grid-row-gap: 2rem;
    row-gap: 2rem;
    margin-bottom: 1.5rem;
}

.footer-list-container {
    display: flex;
    justify-content: center;
    width: 100%;
}

.footer-list-container div > img{
    height: 2.3rem;
    margin-bottom: .5rem;
}

.footer-list-container a {
    color: #9796a2;
    margin-bottom: 0.4rem;
    position: relative;
    transition: all .4s ease;
}

.footer-list-container a:hover {
    color: white;
    padding-left: 14px;
}

.footer-list-container a:hover::before {
    opacity: 1;
}

.footer-list-container a::before {
    content: '';
    width: 8px;
    height: 3px;
    background-color: var(--thm-primary);
    position: absolute;
    border-radius: 2px;
    top: 50%;
    left: 0;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    opacity: 0;
    transition: all .5s ease;
}

.footer-list-container h3 {
    margin-bottom: 1rem;
    font-weight: 600;
    color: white;
}

.footer-list-container>ul>li {
    display: flex;
    flex-direction: column;
    font-weight: 500;
}

#contact li>span {
    margin-left: 10px;
}

.icons-contact-footer {
    color: var(--thm-primary)
}

#contact li {
    min-width: 15rem;
    display: block;
    margin-bottom: 0.5rem;
}

.companyName {
    color: var(--thm-primary);
}


/* ============ Media queries (Smal devices) =============== */
@media screen and (max-width: 792px) {
    .footer-list-container {
        justify-content: flex-start;
    }

    .show-copy {
        display: none;
    }
}
header {
    width: 100%;
    height: 72px;
    position: relative;
    background-color: white;  
}

.header-area {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0 auto;
    
}

.brand-container {
    display: flex;
    align-items: center;
    height: 45px;
}

.brand-container > img {
    height: 100%;
    object-fit: cover;
}

.navbar-main {
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.menu-list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 1.15rem;
    grid-gap: 1.4rem;
    gap: 1.4rem;  
}

.menu-list a {
    height: 67px;
    font-weight: 500;
    color: var(--thm-black);
    display: flex;
    align-items: center;
    border: 3px solid transparent;
    border-radius: 4px;
    transition: var(--trasition);
}

.menu-list a:hover {
    color: var(--thm-primary);
    border-bottom: 3px solid var(--thm-primary);
}

.activeLink[data-active] {
    color: var(--thm-primary);
}

.icons-container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 3rem; 
    grid-gap: 1.5rem; 
    gap: 1.5rem; 
}

.icons-styles {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.8rem;
    color: var(--thm-primary);
    cursor: pointer;
    transition: var(--trasition);
}

.user-icon {
    height: 32.8px;
    border: 2px solid var(--thm-primary);
    border-radius: 50%;
}

.user-icon:hover {
    background-color: var(--thm-primary);
    color: white;
}

.menu-icon {
    font-size: 2.5rem;
    margin-right: .5rem;
    display: none;
}


@media screen and (max-width: 834px) {

    .menu-list {
        display: none;
    }

    .menu-icon {
        display: block;
    }
    
    .icons-container {
        margin-left: 2rem;
    }

}
.mobile-nav-container {
