    background-color: rgb(140, 140, 146);;
    color: var(--thm-base);
}

/* ############ List-ComboBox ############ */

.combobox-list {
    position: absolute;
    overflow: auto;
    /* min-width: 450px; */
    width: 100%;
    max-height: 550px;
    margin-top: 0.25rem;
    padding: 0.5rem;
    background-color: var(--thm-base);
    border-radius: 1rem;
    border: 0px solid transparent;
    box-shadow: rgb(0 0 0 / 4%) 0px 10px 20px,
        rgb(0 0 0 / 4%) 0px 2px 10px,
        rgb(0 0 0 / 2%) 0px 0px 4px;
    z-index: 2;
}

.combobox-list > div {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    color: rgb(60, 60, 60);
    padding: 0.75rem 1rem;
    cursor: pointer;
    border-radius: var(--thm-border-radius);
    transition: all 0.25s ease;
}

.combobox-item:hover {
