}

.combobox-item:hover .icon-list-conatiner {
    background-color: var(--thm-base);
}

.combobox-item[data-active-item] {
    background-color: var(--thm-gray-form);
}

.combobox-item[data-active-item] .icon-list-conatiner {
    background-color: var(--thm-base);
}

.combobox-item [data-user-value] {
    font-weight: 700;
    font-size: 1.13rem;    
}

.icon-list-conatiner {
    width: 2rem;
    height: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    margin-right: 0.75rem;
    padding: 0.5rem;
    background-color: var(--thm-gray-form)
}

.text-list-container {
    font-size: 1.1rem; 
    line-height: 1.32rem; 
    text-align: left;
}

.regionText {
    display: block;
    font-size: 0.93rem;
    color: #8c8c92;
    /* text-align: left; */
}

.date-picker-container {
    width: 100%;
