/* ///////// Marker ////////// */

.price-tag {
    position: relative;
    height: 1rem;
    width: 1rem;

}

.price_marker {
    position: absolute;
    top: -91%;
    left: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    min-width: 40px;
    border-radius: .5rem;
    cursor: pointer;
    color: #3c3c3c;
    font-size: 16px;
    padding: 6px 8px;
    font-weight: 700;
    white-space: noWrap;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -webkit-filter: drop-shadow(rgba(0, 0, 0, 0.04) 0px 4px 8px) 
    drop-shadow(rgba(0, 0, 0, 0.19) 0px 0px 2px) 
    drop-shadow(rgba(0, 0, 0, 0.341) 0px 0px 1px);
            filter: drop-shadow(rgba(0, 0, 0, 0.04) 0px 4px 8px) 
    drop-shadow(rgba(0, 0, 0, 0.19) 0px 0px 2px) 
    drop-shadow(rgba(0, 0, 0, 0.341) 0px 0px 1px);
    border: none;
    outline-style: none;
}


.price_marker::after {
    content: "";
    position: absolute;
    left: 50%;
    top: 100%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
    width: 0;
    height: 0;
    border-left: 8px solid transparent;
    border-right: 8px solid transparent;
    border-top: 8px solid #fff;
}


.bounce {
    -webkit-animation-name: bounce;
            animation-name: bounce;
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-duration: 1.35s;
            animation-duration: 1.35s;
    background-color: #4286f4;
    color: #fff;
    z-index: 1;
}

.bounce::after {
    content: "";
    position: absolute;
    left: 50%;
    top: 100%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
    width: 0;
    height: 0;
    border-left: 8px solid transparent;
    border-right: 8px solid transparent;
    border-top: 8px solid #4285F4; 
}

.highlight {
    background-color: #4286f4;
    color: #fff;
    z-index: 1;
}

.highlight::after {
    content: "";
    position: absolute;
    left: 50%;
    top: 100%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
    width: 0;
    height: 0;
    border-left: 8px solid transparent;
    border-right: 8px solid transparent;
    border-top: 8px solid #4285F4; 
}

@-webkit-keyframes bounce {

    0% {
        -webkit-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
    }

    55% {
        -webkit-transform: translate(-50%, -78%);
                transform: translate(-50%, -78%);
    }

    100% {
        -webkit-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
    }
}

@keyframes bounce {

    0% {
        -webkit-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
    }

    55% {
        -webkit-transform: translate(-50%, -78%);
                transform: translate(-50%, -78%);
    }

    100% {
        -webkit-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
    }
}




