@font-face {
  font-family: reeyregular;
  src: url(/static/media/Reey-Regular.a2dba68c.otf);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
  scrollbar-width: thin;
  scrollbar-color: rgb(158, 158, 158) #f1f1f1;;
}

:root {
  --thm-font: "DM Sans", sans-serif;
  --thm-reey-font: "reeyregular";
  --thm-gray: #787780;
  --thm-gray-rgb: 120, 119, 128;
  --thm-gray-form: rgb(241, 240, 240);
  --thm-primary: #e8604c;
  --thm-primary-rgb: 232, 96, 76;
  --thm-black: #313041;
  --thm-black-form: #3c3c3c;
  --thm-black-rgb: 49, 48, 65;
  --thm-base: #fff;
  --thm-base-rgb: 255, 255, 255;
  --thm-clr-extra: #e3e3e3;
  --thm-border-radius: 0.5rem;
  --thm-letter-spacing: -0.02em;

  --container-width-lg: 70%;
  --container-width-md: 85%;
  --container-width-sm: 95%;
  
  --trasition: all 500ms ease;
}

html {
  scroll-behavior: smooth;
}

body {
  /* margin: 0 2rem; */
  font-family: "DM Sans", sans-serif;
  font-family: var(--thm-font);
  color: #787780;
  color: var(--thm-gray);
  font-size: 18px;
  line-height: 32px;
  font-weight: 400;
}

/* ============ GENERAL STYLES ========= */
.container {
  width: 85%;
  width: var(--container-width-md);
  max-width: 1220px;  /*1320 original*/
}

.main_container {
  width: 85%;
  width: var(--container-width-md);
  max-width: 1090px;
}

h1, h2, h3, h4, h5, h6 {
  color: #313041;
  color: var(--thm-black);
}

h1 {
  font-size: clamp(1.6rem, 3vw, 4rem);
}

h2 {
  margin: 0;
  font-weight: 700;
  font-size: clamp(1.5rem, 4vw, 2.5rem);
  line-height: 3.6rem;
}

a,
a:hover,
a:focus,
a:visited {
  text-decoration: none;
}


@media screen and (max-width: 1200px) {

  .container {
    width: 95%;
    width: var(--container-width-sm)
  }

}

@media screen and (max-width: 1000px) {

  .main_container {
    width: 95%;
    width: var(--container-width-sm);
  }
}

/* ///////// SCROLLBAR ////////// */
/* width */
::-webkit-scrollbar {
  width: .4rem;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: rgb(175, 171, 171);
  border-radius: .2rem;
}


